html {
  box-sizing: border-box;
}

* ::before,
::after {
  box-sizing: inherit;
}

p,
span,
h1,
h2,
h3 {
  margin: 0;
  padding: 0;
}

body {
  background-color: #d1e7e0;
  color: #463558;
  font-family: "Montserrat", Arial, "sans-serif";
  font-size: 16px;
}

img {
  max-width: 100%;
}

.container {
  padding: 20px;
}

.navigation {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 15px;
}

.navigation__logo {
  font-size: 30px;
  font-weight: 700;
}

.navigation__items {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.navigation__item {
  color: #463558;
  padding: 8px;
  font-size: 18px;
  font-weight: 700;
  text-decoration: none;
}
.navigation__item:hover {
  color: #d1e7e0;
  background-color: #463558;
  text-decoration: underline;
}

.about {
  text-align: center;
}

.about--headline {
  font-size: 28px;
  color: #161718;
  font-weight: 600;
  text-align: center;
}

.hero {
  background-image: url("/images/library.jpg");
  width: 1100px;
  height: 590px;
}

.intro {
  display: flex;
  flex-direction: column;
}
.intro__background {
  background-color: #463558;
  color: #ffffff;
}

.intro__image,
.intro__text {
  flex-basis: 50%;
}

h2 {
  font-size: 24px;
  color: #161718;
  font-weight: 600px;
  text-align: center;
}

h3 {
  font-size: 21px;
  color: #161718;
  font-weight: 600px;
  text-align: center;
}

.columns {
  display: flex;
}
.columns--icon {
  width: 20px;
  height: auto;
}
.columns--item {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.button {
  padding: 10px 20px;
  background-color: #463558;
  color: #ffffff;
  display: inline-block;
}

.footer {
  display: flex;
  flex-direction: space-between;
}